var { div, section, input, textarea, h2 } = require('hyperaxe')

module.exports = function Update (state, emit) {
  return div(
    { id: 'edge-update', className: 'edge-update ' },
    div(
      { className: 'w-100' },
      h2({ className: 'dib mv0 f2 tracked' }, 'Software Update'),
      status(state, emit),
      textarea({
        attrs: { readonly: true, rows: 8 },
        className: 'w-100 pa2 code lh-copy',
        value: `${state.updateLog.join('\n')}`
      })
    )
  )
}

function status (state, emit) {
  return state.updateMachine.cata({
    NotAsked: NotAsked,
    Checking: () => div({}, 'Checking...'),
    Outdated: Outdated,
    Current: () => div({}, 'No Updates'),
    Updating: () => div({}, 'Updating...'),
    Error: () => div({}, 'Something went wrong'),
    Success: () => div({}, 'Success! Refresh browser please'),
    Failed: m => div({}, m)
  })

  function NotAsked () {
    return section(
      { className: 'pv1' },
      input({
        className: 'f5 w5',
        id: 'update-get-submit',
        type: 'submit',
        value: 'Check for Updates',
        onclick: update
      }),
      input({
        className: 'f5 w5',
        id: 'upgrade-get-submit',
        type: 'submit',
        value: 'Update',
        onclick: upgrade
      })
    )

    function update () {
      emit(state.events.UPDATE_GET)
    }

    function upgrade () {
      emit(state.events.UPGRADE_GET)
    }
  }

  function Outdated (version) {
    return div(
      {},
      input({
        className: 'w-100 tc f6 ba bw1 ph3 pv2 mb2 dib mid-gray',
        id: 'update-do-submit',
        type: 'submit',
        value: `Update to ${version}`,
        onclick
      })
    )

    function onclick () {
      emit(state.events.UPDATE_POST)
    }
  }
}
