var daggy = require('daggy')
var request = require('request')

module.exports = makeRemote

function makeRemote (path, state, effect) {
  var RemoteData = daggy.taggedSum('RemoteData', {
    NotLoaded: [],
    Loading: [],
    Error: ['err'],
    Failure: ['value'],
    Success: ['value']
  })

  state[path] = RemoteData.NotLoaded

  RemoteData.prototype.get = function (cb) {
    state[path] = RemoteData.Loading
    effect()

    request.get(`/api/${path}`, { json: true }, function (err, res, body) {
      if (err) {
        state[path] = RemoteData.Error(err)
      } else if (res.statusCode !== 200) {
        state[path] = RemoteData.Failure(body)
      } else {
        state[path] = RemoteData.Success(body)
      }

      effect()

      if (typeof cb === 'function') {
        return cb(state[path])
      }
    })
  }

  RemoteData.prototype.post = function (data, cb) {
    state[path] = RemoteData.Loading
    effect()

    request.post(`/api/${path}`, { json: true, data: data }, function (
      err,
      res,
      body
    ) {
      if (err) {
        state[path] = RemoteData.Error(err)
      } else if (res.statusCode !== 200) {
        state[path] = RemoteData.Failure(body)
      } else {
        state[path] = RemoteData.Success(body)
      }

      effect()

      if (typeof cb === 'function') {
        return cb(state[path])
      }
    })
  }

  RemoteData.prototype.put = function (data, cb) {
    state[path] = RemoteData.Loading
    effect()

    request.put(`/api/${path}`, { json: true, data: data }, function (
      err,
      res,
      body
    ) {
      if (err) {
        state[path] = RemoteData.Error(err)
      } else if (res.statusCode !== 200) {
        state[path] = RemoteData.Failure(body)
      } else {
        state[path] = RemoteData.Success(body)
      }

      effect()

      if (typeof cb === 'function') {
        return cb(state[path])
      }
    })
  }

  RemoteData.prototype.delete = function (cb) {
    state[path] = RemoteData.Loading
    effect()

    request.del(`/api/${path}`, { json: true }, function (err, res, body) {
      if (err) {
        state[path] = RemoteData.Error(err)
      } else if (res.statusCode === 204) {
        state[path] = RemoteData.Failure('Deleted')
      } else if (res.statusCode !== 200) {
        state[path] = RemoteData.Failure(body)
      } else {
        state[path] = RemoteData.Success(body)
      }

      effect()

      if (typeof cb === 'function') {
        return cb(state[path])
      }
    })
  }

  RemoteData.prototype.isLoading = function () {
    return this.cata({
      NotLoaded: F,
      Loading: T,
      Success: F,
      Error: F
    })
  }

  return state[path]
}

function F () {
  return false
}
function T () {
  return true
}
