var { section, div } = require('hyperaxe')

var TITLE = 'ZeroTier - Edge - User'

var ChangePassword = require('./password')
var ManageSSHKeys = require('./manage-ssh-keys')
var Layout = require('../components/layout')
var Login = require('../session/view')

var manageSSHKeys = new ManageSSHKeys()

module.exports = function mainView (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)

  var layout = Layout(state, emit)

  return state.sessions.cata({
    NotLoaded: login,
    Loading: login,
    Error: login,
    Failure: login,
    Success: () => layout(view)
  })

  function view (state, emit) {
    return section(
      { className: 'user-view' },
      section(
        { id: 'change-password', className: 'cf pb4' },
        div(
          changePassword()
        )
      ),
      section(
        { id: 'ssh-keys', className: 'cf pv4' },
        div(
          manageSSHKeys.render(state, emit)
        )
      )
    )
  }

  function changePassword () {
    return state.sessions.cata({
      NotLoaded: () => {},
      Loading: () => {},
      Error: () => {},
      Failure: () => 'You need to login!',
      Success: () => ChangePassword(state, emit)
    })
  }

  function login () {
    return Login(state, emit)
  }
}
