var { header, div, h2, p, form, fieldset, label, input } = require('hyperaxe')

var Spinner = require('../components/spinner')

module.exports = function LoginForm (state, emit) {
  var oldPasswordId = 'old-password'
  var newPassword1id = 'new-password1'
  var newPassword2id = 'new-password2'

  var data = {}

  return div(
    { id: 'edge-login' },
    div(
      { className: 'w-100' },

      div(message()),
      form(
        { onsubmit: submit, id: 'change-pw-form', className: 'bn ma0 pa0' },
        header(
          { className: 'h4' },
          h2({ className: 'dib mv0 f2 tracked' }, 'Change Password'),
          div(
            { className: 'dib fr' },
            input({
              id: 'password-submit',
              type: 'submit',
              value: 'Submit'
            }),
            div(
              { className: 'di absolute' },
              Spinner(isLoading(state))
            )
          ),
          p({ className: 'f4' }, `For: ${state.sessions.value.username}`)
        ),

        fieldset(
          { className: 'bn mh0 ph0' },
          label(
            { className: 'f5 w5 dib', attrs: { for: oldPasswordId } },
            'Current Password'
          ),
          input({
            className: 'w5 dib',
            type: 'password',
            minlength: 4,
            autocomplete: 'current-password',
            required: true,
            onblur: onblurOld,
            id: oldPasswordId
          }),
          div({ className: 'h2 f7 light-red', id: 'old-password-message' })
        ),
        fieldset(
          { className: 'bn mh0 ph0' },
          label(
            { className: 'f5 w5 dib', attrs: { for: newPassword1id } },
            'New Password'
          ),
          input({
            className: 'w5 dib',
            type: 'password',
            minlength: 4,
            required: true,
            onblur: onblurNew1,
            id: newPassword1id
          }),
          div({ className: 'h2 f7 light-red', id: 'new-password1-message' })
        ),
        fieldset(
          { className: 'bn mh0 ph0' },
          label(
            { className: 'f5 w5 dib', attrs: { for: newPassword2id } },
            'Confirm New Password'
          ),
          input({
            className: 'w5 dib',
            type: 'password',
            minlength: 4,
            required: true,
            onblur: onblurNew2,
            id: newPassword2id
          }),
          div({ className: 'h2 f7 light-red', id: 'new-password2-message' })
        )
      )
    )
  )

  function message () {
    return state.passwords.cata({
      NotLoaded: () => {
        return ''
      },
      Loading: () => 'Changing Password...',
      Error: err,
      Failure: fail,
      Success: () => {
        return 'Success! New password saved.'
      }
    })

    function fail (e) {
      return div(
        { className: 'w-100 bg-yellow pa3 mv2' },
        'Error saving password'
      )
    }

    function err (e) {
      return div(
        { className: 'w-100 bg-yellow pa3 mv2' },
        'Something went wrong'
      )
    }
  }

  function onblurOld (ev) {
    data.password = ev.target.value
  }

  function onblurNew1 (ev) {
    data.newPassword1 = ev.target.value
  }

  function onblurNew2 (ev) {
    data.newPassword2 = ev.target.value

    var pw1 = document.querySelector('#' + newPassword1id)
    var pw2 = document.querySelector('#' + newPassword2id)
    if (pw1.value === pw2.value) {
      pw2.setCustomValidity('')
    } else {
      pw2.setCustomValidity('Password fields must match')
    }
  }

  function submit (ev) {
    ev.preventDefault()
    if (ev.target.checkValidity()) {
      emit(state.events.PASSWORD_UPDATE, data)
    }
  }
}

function isLoading (state) {
  var machines = [state.passwords]

  return machines.some(function (m) {
    return m.isLoading()
  })
}
