var Edge = require('../settings/remote.js')('Edge')
var onIdle = require('on-idle')

module.exports = function (api) {
  return function store (state, emitter) {
    state.edge = {}
    state.edgeMachine = Edge.NotAsked
    state.edgeMachineVector = 0
    state.edgeEdited = false
    state.httpAdminAddresses = {}
    state.httpAdminEdited = false
    state.httpAdminMachine = 'NotAsked'

    state.events.EDGE_GET = 'edge:get'
    state.events.EDGE_EDIT = 'edge:edit'
    state.events.EDGE_EDIT_DNS = 'edge:editdns'
    state.events.EDGE_SUBMIT = 'edge:submit'

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.NAVIGATE, navigate)
      emitter.on(state.events.EDGE_EDIT, edit)
      emitter.on(state.events.EDGE_EDIT_DNS, editDns)
      emitter.on(state.events.EDGE_SUBMIT, submit)

      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()
        onIdle(function () {
          get()
        })
      })
    })

    function edit (changes) {
      state.edge = Object.assign(state.edge, changes)
      state.edgeEdited = true

      render()
    }

    function editDns (idx, value) {
      state.edgeEdited = true
      state.edge.dns = state.edge.dns || []
      state.edge.dns.splice(idx, 1, value)

      state.edge.dns = state.edge.dns.filter(function (x) {
        return x
      })

      render()
    }

    function get () {
      state.edgeMachine = Edge.Loading
      render()

      api.get('edge', function (err, res) {
        if (err) {
          state.edgeMachine = Edge.Error(err)
          state.edge = {}
        } else {
          if (res.statusCode === 200) {
            state.edgeMachine = Edge.Success(res.body || {})
            state.edge = res.body || {}
          } else {
            state.edgeMachine = Edge.Error(res.body)
            state.edge = {}
          }
        }

        render()
      })
    }

    function submit () {
      state.edgeMachine = Edge.Saving(state.edge)
      render()

      api.put('edge', state.edge, function (err, res) {
        if (err) {
          state.edgeMachine = Edge.Error(err)
        } else {
          if (res.statusCode === 200) {
            state.edgeMachine = Edge.Success(res.body || {})
            state.edge = res.body || {}
            state.edgeEdited = false
          } else {
            state.edgeMachine = Edge.Error(res.body)
          }
        }

        render()
      })
    }

    function navigate () {
      if (state.route === 'device') {
        state.edgeMachine.cata({
          NotAsked: get,
          Loading: noop,
          Success: noop,
          Error: get,
          Saving: noop
        })
      }
    }

    function render () {
      state.edgeMachineVector++
      emitter.emit('render')
    }
  }
}

function noop () {}
