var uuid = require('uuid')
var onIdle = require('on-idle')

var remote = require('./remote')
var { normalize, denormalize } = require('./reshape.js')

var Interfaces = remote('Interfaces')
var OSInterfaces = remote('OS Interfaces')

module.exports = function (api) {
  return function store (state, emitter) {
    state.interfacesClock = 0
    state.interfacesEdited = false
    state.interfacesMachine = Interfaces.NotAsked
    state.osInterfacesMachine = OSInterfaces.NotAsked
    state.interfaces = {}
    state.addresses = {}
    state.osInterfaces = []
    state.prohibit = {}

    state.events.PHYSICAL_GET = 'physical:get'
    state.events.PHYSICAL_SUBMIT = 'physical:put'
    state.events.PHYSICAL_CREATE = 'physical:create'
    state.events.PHYSICAL_DELETE = 'physical:delete'
    state.events.PHYSICAL_UPDATE = 'physical:update'
    state.events.PHYSICAL_AUTO = 'physical:auto'
    state.events.ADDRESS_CREATE = 'address:create'
    state.events.ADDRESS_UPDATE = 'address:update'
    state.events.ADDRESS_DELETE = 'address:delete'
    state.events.BRIDGE_PROHIBIT = 'physical:block'

    state.constants = state.constants || {}
    state.constants.IP_FAMILIES = ['ipv4', 'ipv6']
    state.constants.IP_PROTOS = ['static', 'dhcp']
    state.constants.AUTO_CONFS = ['dhcp4', 'dhcp6', 'slaac']
    state.constants.BRIDGE_PROHIBIT = ['dhcp4', 'dhcp6', 'ipv6-ra']
    state.constants.WIFI_MODES = ['ap', 'client']
    state.constants.WIFI_CHANS = [
      { channel: 1, frequency: '2412 MHz' },
      { channel: 2, frequency: '2417 MHz' },
      { channel: 3, frequency: '2422 MHz' },
      { channel: 4, frequency: '2427 MHz' },
      { channel: 5, frequency: '2432 MHz' },
      { channel: 6, frequency: '2437 MHz' },
      { channel: 7, frequency: '2442 MHz' },
      { channel: 8, frequency: '2447 MHz' },
      { channel: 9, frequency: '2452 MHz' },
      { channel: 10, frequency: '2457 MHz' },
      { channel: 11, frequency: '2462 MHz' },
      { channel: 36, frequency: '5180 MHz' },
      { channel: 40, frequency: '5200 MHz' },
      { channel: 44, frequency: '5220 MHz' },
      { channel: 48, frequency: '5240 MHz' },
      { channel: 149, frequency: '5745 MHz' },
      { channel: 153, frequency: '5765 MHz' },
      { channel: 157, frequency: '5785 MHz' },
      { channel: 161, frequency: '5805 MHz' },
      { channel: 165, frequency: '5825 MHz' }
    ]

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.NAVIGATE, navigate)
      emitter.on(state.events.PHYSICAL_CREATE, create)
      emitter.on(state.events.PHYSICAL_UPDATE, update)
      emitter.on(state.events.PHYSICAL_AUTO, autoConf)
      emitter.on(state.events.PHYSICAL_SUBMIT, put)
      emitter.on(state.events.PHYSICAL_DELETE, destroy)
      emitter.on(state.events.BRIDGE_PROHIBIT, block)

      emitter.on(state.events.ADDRESS_CREATE, addressCreate)
      emitter.on(state.events.ADDRESS_UPDATE, addressUpdate)
      emitter.on(state.events.ADDRESS_DELETE, addressDestroy)

      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()

        onIdle(function () {
          get()
        })
      })
    })

    function create (ifaceName) {
      state.interfaces[ifaceName] = templateInterface(ifaceName)
      state.interfacesEdited = true

      render()
    }

    function update (ifaceName, changes) {
      state.interfaces[ifaceName] = Object.assign(
        state.interfaces[ifaceName],
        changes
      )
      state.interfacesEdited = true

      render()
    }

    function block (iface, name, checked) {
      state.interfaces[iface].prohibit = state.interfaces[iface].prohibit || []
      state.interfacesEdited = true

      if (checked) {
        state.interfaces[iface].prohibit.push(name)
      } else {
        state.interfaces[iface].prohibit = state.interfaces[iface].prohibit.filter(function (x) {
          return x !== name
        })
      }

      render()
    }

    function autoConf (iface, name, checked) {
      state.interfaces[iface].autoconf = state.interfaces[iface].autoconf || []
      state.interfacesEdited = true

      if (checked) {
        state.interfaces[iface].autoconf.push(name)
      } else {
        state.interfaces[iface].autoconf = state.interfaces[iface].autoconf.filter(function (x) {
          return x !== name
        })
      }

      render()
    }

    function destroy (iface) {
      delete state.interfaces[iface]
      state.interfacesEdited = true

      render()
    }

    function addressDestroy (addressId) {
      delete state.addresses[addressId]
      state.interfacesEdited = true

      render()
    }

    function addressCreate (ifaceName) {
      var id = uuid()
      state.addresses[id] = defaultAddr(ifaceName, id)
      state.interfacesEdited = true

      render()
    }

    function addressUpdate (addressKey, prop, val) {
      // (uuid, proto, ipv4)
      // (uuid, family, static)
      state.addresses[addressKey][prop] = val
      state.interfacesEdited = true

      render()
    }

    function get () {
      state.interfacesMachine = Interfaces.Loading
      render()

      api.get('physical', function (err, res) {
        if (err) {
          state.interfacesMachine = Interfaces.Error(err)
        } else {
          if (res.statusCode === 200) {
            state.interfacesMachine = Interfaces.Success(res.body)
            var { interfaces, addresses } = normalize(res.body || [])
            state.interfaces = interfaces
            state.addresses = addresses
            state.interfacesEdited = false
          } else {
            state.interfacesMachine = Interfaces.Error(res.body)
          }
        }
        render()
      })
    }

    function put () {
      var edits = denormalize(state.interfaces, state.addresses)
      state.interfacesMachine = Interfaces.Saving(edits)

      render()
      api.put('physical', edits, function (err, res) {
        if (err) {
          state.interfacesMachine = Interfaces.Error(new Error('Network Error.'))
        } else {
          if (res.statusCode === 200) {
            state.interfacesMachine = Interfaces.Success(res.body || [])
            var { interfaces, addresses } = normalize(res.body || [])
            state.interfaces = interfaces
            state.addresses = addresses
            state.interfacesEdited = false
          } else {
            state.interfacesMachine = Interfaces.Error(res.body)
          }
        }

        render()
      })
    }

    function getOsInterfaces () {
      state.osInterfacesMachine = OSInterfaces.Loading
      render()
      api.get('interfaces', function (err, res) {
        if (err) {
          state.osInterfacesMachine = OSInterfaces.Error(err)
          state.osInterfaces = []
        } else {
          if (res.statusCode === 200) {
            state.osInterfacesMachine = OSInterfaces.Success(res.body || [])
            state.osInterfaces = res.body || []
          } else {
            state.osInterfacesMachine = OSInterfaces.Error(res.body)
            state.osInterfaces = []
          }
        }
        render()
      })
    }

    function navigate () {
      if (state.route === 'settings' ||
          state.route === '/') {
        state.interfacesMachine.cata({
          NotAsked: get,
          Loading: noop,
          Success: noop,
          Error: get,
          Saving: noop
        })

        state.osInterfacesMachine.cata({
          NotAsked: getOsInterfaces,
          Loading: noop,
          Success: noop,
          Error: getOsInterfaces,
          Saving: noop
        })
      }
    }

    function render () {
      state.interfacesClock++
      emitter.emit('render')
    }
  }
}

function templateInterface (name) {
  return {
    name,
    addresses: [],
    type: 'ethernet',
    autoconf: ['dhcp4', 'dhcp6', 'slaac'],
    prohibit: ['dhcp4', 'dhcp6', 'ipv6-ra'],
    mode: 'ap'
  }
}

function defaultAddr (name, id) {
  return {
    proto: 'static',
    family: 'ipv4',
    mask: 24,
    gateway: '',
    address: '',
    iface: name,
    tempId: id
  }
}

function noop () {}
