var { body, div, header, nav } = require('hyperaxe')
var css = require('sheetify')

var nodeInfo = require('../components/header')
var navbar = require('../components/navbar')

var wrap = css`
body > div { width: 100%; padding: 1rem; }
a { color: gold; }
`

module.exports = function Layout (state, emit) {
  return function (child) {
    return body(
      { className: 'white bg-mid-gray avenir' },

      div(
        { className: wrap + ' ph6-l ph3 bg-mid-gray' },
        header(
          { id: 'header', className: 'pt6' },
          nodeInfo(state, emit)
        ),

        div(
          { className: 'pt6' },
          nav(
            { id: 'main',
              className: 'top-0 fixed static-l fl w-20-l w-100 dib bg-mid-gray' },
            navbar(state, emit)
          ),
          div(
            { className: 'dib w-80' },
            main(state, emit)
          )
        )
      )
    )

    function main (state, emit) {
      return state.sessions.cata({
        NotLoaded: loading,
        Loading: loading,
        Error: renderChild,
        Failure: renderChild,
        Success: renderChild
      })

      function renderChild () {
        return child(state, emit)
      }
    }
  }
}

function loading () {
  return div({ className: 'small progress' }, div({}, 'loading...'))
}
