module.exports = function (api) {
  return function store (state, emitter) {
    state.httpAdminAddresses = {}
    state.httpAdminEdited = false
    state.httpAdminLoading = false
    state.httpAdminError = null
    state.httpAdminVector = 0

    state.events.HTTP_LISTEN_LOAD = 'httpadmin:load'
    state.events.HTTP_LISTEN_EDIT = 'httpadmin:listen'
    state.events.HTTP_LISTEN_SUBMIT = 'httpadmin:submit'
    state.events.HTTP_LISTEN_RESTART = 'httpadmin:restart'

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.HTTP_LISTEN_EDIT, toggle)
      emitter.on(state.events.HTTP_LISTEN_SUBMIT, submit)
      emitter.on(state.events.HTTP_LISTEN_RESTART, restart)
      emitter.on(state.events.HTTP_LISTEN_LOAD, load)

      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()
      })
    })

    function toggle (address) {
      var addr = address.address

      if (state.httpAdminAddresses[addr]) {
        delete state.httpAdminAddresses[addr]
      } else {
        state.httpAdminAddresses[addr] = true
      }
    }

    function submit () {
      state.httpAdminLoading = true
      render()
      api.post('http-listen', state.httpAdminAddresses, function (err, res) {
        state.httpAdminLoading = false
        if (err) {
          state.httpAdminError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.httpAdminAddresses = res.body
            state.httpAdminError = null
          } else {
            state.httpAdminError = res.body.message || 'Something went wrong'
          }
        }
        render()
      })
    }

    function restart () {
      state.httpAdminLoading = true
      render()
      api.post('restart', {}, function (err, res) {
        state.httpAdminLoading = false
        if (err) {
          state.httpAdminError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.httpAdminError = null
            setTimeout(() => {
              emitter.emit(state.events.SESSIONS_LOGIN)
            }, 4000)
          } else {
            state.httpAdminError = res.body.message || 'Something went wrong'
          }
        }
        setTimeout(render, 1000)
      })
    }

    function render () {
      state.httpAdminVector++
      emitter.emit('render')
    }

    function navigate () {
      if (state.route === 'device') {
        load()
      }
    }

    function load () {
      state.httpAdminLoading = true
      render()
      api.get('http-listen', function (err, res) {
        state.httpAdminLoading = false
        if (err) {
          state.httpAdminError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.httpAdminAddresses = res.body
            state.httpAdminError = null
          } else {
            state.httpAdminError = res.body.message || 'Something went wrong'
          }
        }
        render()
      })
    }
  }
}
