var { h2, form, fieldset, label, div, input } = require('hyperaxe')

module.exports = function LoginForm (state, emit) {
  var password1 = 'login-password'
  var usernameId = 'login-username'

  return div(
    { className: 'edge-login', id: 'edge-login' },
    div(
      h2({ className: 'dib mt0 f2 tracked' }, 'Login'),
      div(
        {},
        state.sessions.cata({
          NotLoaded: () => {
            return '??'
          },
          Loading: () => 'Authenticating...',
          Error: _ => 'Something went wrong',
          Failure: err => (err.statusCode === 401 ? err.message : ''),
          Success: () => {
            return 'Success!'
          }
        })
      ),
      form(
        { onsubmit: submit, id: 'login-form', className: 'bn ma0 pa0 mt4' },
        fieldset(
          { className: 'bn mh0 ph0' },
          label({ className: 'f5 w5 db', attrs: { for: usernameId } }, 'Username'),
          input({
            className: 'w5 db',
            type: 'text',
            id: usernameId,
            required: true,
            minlength: 4,
            maxlength: 32
          }),
          div({
            className: 'h2 f7 light-red',
            id: 'login-username-message'
          })
        ),
        fieldset(
          { className: 'bn mh0 ph0' },
          label({ className: 'f5 w5 db', attrs: { for: password1 } }, 'Password'),
          input({
            className: 'w5 db',
            type: 'password',
            id: password1,
            required: true
          }),
          div({
            className: 'h2 f7 light-red',
            id: 'login-username-message'
          })
        ),
        input({
          className: 'w4 dib',
          id: 'login-submit',
          type: 'submit',
          value: 'Submit'
        })
      )
    )
  )

  function submit (ev) {
    ev.preventDefault()

    if (ev.target.checkValidity()) {
      var password = document.querySelector('#' + password1).value
      var username = document.querySelector('#' + usernameId).value

      var data = { password: password, username: username }

      emit(state.events.SESSIONS_CREATE, data)
    }
  }
}
