var Nanocomponent = require('nanocomponent')
var nanologger = require('nanologger')
var log = nanologger('status')
var { section, div, span, h2, h3, h4 } = require('hyperaxe')

var Layout = require('../components/layout')
var Login = require('../session/view')

var TITLE = 'ZeroTier - Edge - Status'

class Peerz extends Nanocomponent {
  constructor (opts) {
    if (!opts) opts = {}
    super(opts)
    this.clock = 0
  }

  createElement (state) {
    log.debug('createElement()')

    return ListPeers(state)
  }

  update (state) {
    log.debug('update()')

    var render = this.clock !== state.listPeersClock
    this.clock = state.listPeersClock

    return render
  }

  load () {
    log.debug('load()')
  }
}

module.exports = function (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)
  var layout = Layout(state, emit)

  return state.sessions.cata({
    NotLoaded: login,
    Loading: login,
    Error: login,
    Failure: login,
    Success: () => layout(view)
  })

  function view () {
    return section(
      { },
      div(
        { id: 'settings', className: 'cf' },
        h2({ className: 'dib mv0 f2 tracked' }, 'Status'),
        div(
          { className: 'fl w-100 mb4' },
          div(
            { className: 'mt4 code f7' },
            OsAddresses(state, emit),
            ListNetworks(state, emit),
            state.cache(Peerz, 'peers').render(state)
          )
        )
      )
    )
  }

  function login () {
    return Login(state, emit)
  }
}

function ListPeers (state) {
  var { listPeers: peers = [] } = state
  return section(
    { id: 'list-peers' },
    h3({ className: 'dib mv0 f3 tracked' }, `ZeroTier Peers [${peers.length || 0}]`),
    peers
      .sort(function (a, b) {
        return a.address > b.address ? 1 : -1
      })
      .map(function (peer) {
        return section(
          { id: `peer-${peer.address}`, className: 'mv4', style: 'word-wrap: break-word;' },
          dump(peer)
        )
      })
  )
}

function ListNetworks (state, emit) {
  return section(
    { id: 'list-networks' },
    h3({ className: 'dib mv0 f3 tracked' }, 'ZeroTier Networks'),
    state.listNetworks
      .sort(function (a, b) {
        return a.id > b.id ? 1 : -1
      })
      .map(function (network) {
        return section(
          { className: 'mv4', style: 'word-wrap: break-word;' },
          h3(`${network.id}`),
          h4(`${network.name}`),
          dump(network)
        )
      })
  )
}

function OsAddresses (state, emit) {
  return section(
    { id: 'list-phy' },
    h3({ className: 'dib mv0 f3 tracked' }, 'Interfaces'),
    Object.keys(state.osAddresses)
      .sort()
      .map(function (iface) {
        return section(
          { className: 'mv4' },
          h3(iface),
          dump(state.osAddresses[iface])
        )
      })
  )
}

function dump (obj) {
  if (obj == null) { return '-' }

  if (Array.isArray(obj)) {
    return div(
      {},
      obj
        .map(function (o) {
          return div({ className: 'pl4 mb2' }, dump(o))
        })
    )
  } else if (typeof obj === 'object') {
    return div(
      {},
      Object.keys(obj)
        .map(function (k) {
          return div(
            {},
            span({className: 'dib mr2 w5'}, `${k}: `),
            dump(obj[k])
          )
        })
    )
  } else {
    return span({}, obj)
  }
}
