var uuid = require('uuid').v4

module.exports = { normalize, denormalize }

function normalize (interfacesArr) {
  var addresses = interfacesArr.reduce(function (acc, iface) {
    if (!iface.addresses) return acc

    var ifaceAddrs = iface.addresses.reduce(function (acc2, address) {
      var tempId = uuid()

      var addr = Object.assign({}, address, { tempId }, { iface: iface.name })

      return Object.assign({}, acc2, { [tempId]: addr })
    }, {})

    return Object.assign({}, acc, ifaceAddrs)
  }, {})

  var interfaces = interfacesArr.reduce(function (acc, el) {
    var dup = Object.assign({}, el)
    delete dup.addresses

    return Object.assign({}, acc, { [dup.name]: dup })
  }, {})

  return { interfaces, addresses }
}

function denormalize (interfacesDict, addresses) {
  var interfaces = Object.values(interfacesDict).map(function (iface) {
    var ifaceAddresses = Object.values(addresses)
      .filter(function (a) {
        return iface.name && a.iface === iface.name
      })
      .map(function (a) {
        var b = Object.assign({}, a)
        delete b.tempId
        delete b.iface
        return b
      })

    var result = Object.assign({}, iface)

    if (ifaceAddresses.length) {
      result.addresses = ifaceAddresses
    }

    return result
  })

  return interfaces
}
