var { div, section, a, span } = require('hyperaxe')
var classnames = require('classnames')

var bold = 'b'
var linkHover = 'link underline-hover'

module.exports = function NavBar (state, emit) {
  return div(
    { className: 'w-100-l di db-l' },
    NavLinkList(state, emit)
  )
}

function LogoutButton (state, emit) {
  return state.sessions.cata({
    NotLoaded: placeHolder,
    Loading: placeHolder,
    Error: placeHolder,
    Failure: placeHolder,
    Success: SessionInfoSuccess
  })

  function SessionInfoSuccess (sessions) {
    return a(
      {
        id: 'edge-logout',
        href: '',
        onclick: handleClick,
        className: classnames(linkHover)
      },
      'Logout'
    )

    function handleClick () {
      emit(state.events.SESSIONS_DELETE)
    }
  }
}

function NavLinkList (state, emit) {
  var { sessions, init } = state

  return sessions.cata({
    NotLoaded: placeHolder,
    Loading: placeHolder,
    Error: NavLoggedOut(init),
    Failure: NavLoggedOut(init),
    Success: NavLoggedIn(state, emit)
  })
}

function NavLoggedOut (init) {
  return function () {
    return init.cata({
      NotLoaded: Login,
      Loading: Login,
      Error: Login,
      Failure: Login,
      Success: Setup
    })
  }
}

function NavLoggedIn (state, emit) {
  var { route } = state
  route = route === '/' ? 'settings' : route

  var link = navLink(route)

  return function () {
    return section(
      { className: 'pv0-l pv3 bb b--gray bn-l' },
      navWrap(link('settings')),
      // navWrap(link('device')),
      navWrap(link('update')),
      navWrap(link('user')),
      navWrap(link('status')),
      navWrap(LogoutButton(state, emit))
    )
  }
}

var navClass = 'db-l dib mr2 h3-l'
function navWrap (kids) {
  return span({ className: navClass }, kids)
}

function navLink (currentRoute) {
  return function (name) {
    var active = currentRoute === name ? bold : null
    var className = classnames(linkHover, active, ' ttc')

    return a({ id: `nav-${name}`, href: `/${name}`, className }, name)
  }
}

function Setup () {
  return navWrap(navLink('setup')('setup'))
}

function Login () {
  return navWrap(
    span(
      { id: 'nav-login', className: classnames(linkHover, bold) },
      'Login'
    )
  )
}

function placeHolder () {
  return null
}
