var daggy = require('daggy')

module.exports = function (name) {
  var Remote = daggy.taggedSum(name, {
    NotAsked: [],
    Loading: [],
    Success: ['_value'],
    Error: ['_error'],
    Saving: ['_value']
  })

  Remote.prototype.isLoading = function () {
    return this.cata({
      NotAsked: F,
      Loading: T,
      Success: F,
      Error: F,
      Saving: F
    })
  }

  Remote.prototype.isSaving = function () {
    return this.cata({
      NotAsked: F,
      Loading: F,
      Success: F,
      Error: F,
      Saving: T
    })
  }

  return Remote
}

function F () {
  return false
}
function T () {
  return true
}
