var nanologger = require('nanologger')
var log = nanologger('status')
var onIdle = require('on-idle')

var Remote = require('../remote-data')

module.exports = function (api) {
  return function store (state, emitter) {
    state.init = Remote('init', state, update)
    state.status = Remote('status', state, update)
    state.version = process.env.npm_package_version

    state.osAddresses = {}
    state.listNetworks = []
    state.listPeers = []
    state.listPeersClock = 0

    state.events.OS_ADDRESSES_GET = 'osaddresses:get'

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.NAVIGATE, navigate)
      emitter.on(state.events.OS_ADDRESSES_GET, getOSAddresses)

      state.status.get()

      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()

        poll()
      })
    })

    function poll () {
      onIdle(function () {
        get()
        setTimeout(poll, 10 * 1000)
      })
    }

    emitter.on('update', function () {
      emitter.emit(state.events.RENDER)
    })

    function update () {
      emitter.emit('update')
    }

    function navigate () {
      if (state.route === 'status') {
        emitter.emit('render')
        get()
      }
    }

    function getOSAddresses () {
      api.get('addresses', function (err, res) {
        if (err) { log.error(err) }

        if (res.statusCode === 200) {
          state.osAddresses = res.body
          emitter.emit('render')
        }
      })
    }

    function get () {
      api.get('addresses', function (err, res) {
        if (err) { log.error(err) }

        if (res.statusCode === 200) {
          state.osAddresses = res.body
          emitter.emit('render')
        }

        api.get('list-networks', function (err, res) {
          if (err) { log.error(err) }

          if (res.statusCode === 200) {
            state.listNetworks = res.body
            emitter.emit('render')
          }

          api.get('list-peers', function (err, res) {
            if (err) { log.error(err) }

            if (res.statusCode === 200) {
              state.listPeers = res.body
              state.listPeersClock++
              emitter.emit('render')
            }
          })
        })
      })
    }
  }
}
