var { div, section, span, p } = require('hyperaxe')
var classnames = require('classnames')

var color = 'white'
var bold = 'b'

module.exports = function NavBar (state, emit) {
  return div(
    { className: 'w-100' },
    section(
      { className: 'cf' },
      p({ className: 'fl f3 mv0 tracked' }, EdgeVersion(state, emit)),
      p({ className: 'fr f4 mv0 tracked' }, NodeInfo(state, emit))
      // section({ className: 'dib tr fr' }, LogoutButton(state, emit))
    )
  )
}

function EdgeVersion (state, emit) {
  return span({ className: classnames(color, bold) }, `Edge ${state.version}`)
}

function NodeInfo (state, emit) {
  var { status } = state

  return span(
    { className: classnames(color, bold) },
    span(
      { className: 'dib' }, 'Node ID'),
    span(
      { className: 'dib code ttu ml4' },
      status.cata({
        NotLoaded: placeHolder,
        Loading: placeHolder,
        Failure: () => 'Error',
        Error: () => 'Error',
        Success: statusString
      })
    )
  )

  function statusString (status) {
    if (!status.online) {
      return status.address + ' Offline'
    }

    if (status.online && status.tcpFallbackActive) {
      return status.address + ' Relaying'
    }

    return status.address
  }
}

function placeHolder () {
  return null
}
