var Nanocomponent = require('nanocomponent')
var { div, input, textarea, h2, header } = require('hyperaxe')
var nanologger = require('nanologger')
var log = nanologger('ssh')

var Spinner = require('../components/spinner')

class SSHKeys extends Nanocomponent {
  constructor (opts) {
    if (!opts) opts = {}
    super(opts)
    this.newKey = ''
    this.vector = 0
    this.sshKey = this.sshKey.bind(this)
  }

  createElement (state, emit) {
    log.debug('createElement()')

    this.emit = emit
    this.state = state

    var self = this

    return div(
      { id: 'edge-ssh-keys', className: 'flex ' },
      div(
        { className: 'w-100' },
        header(
          h2({ className: 'dib mv0 f2 tracked' }, 'SSH Keys')
        ),
        Error(state.sshKeysError),
        textarea({
          attrs: { rows: 4 },
          className: 'w-100 pa2 code lh-copy',
          onchange: onchange,
          value: this.newKey,
          placeholder: 'ssh-rsa ...'
        }),
        div(
          input({
            onclick,
            value: 'Save Key',
            className: 'w5 mv2 dib',
            type: 'submit'
          }),
          div({ className: 'dib' }, Spinner(state.sshKeysLoading))
        ),
        state.sshKeys.map(this.sshKey)
      )
    )

    function onchange (ev) {
      self.newKey = ev.target.value
    }

    function onclick () {
      self.emit(self.state.events.SSH_KEYS_PUT, self.newKey)
      self.newKey = ''
    }
  }

  update (state, emit) {
    log.debug('update()')

    this.emit = emit
    this.state = state

    if (state.sshKeysVector === this.vector) {
      return false
    } else {
      this.vector = state.sshKeysVector
      return true
    }
  }

  load () {
    log.debug('load()')
  }

  sshKey (key, idx) {
    var self = this
    var id = `key-del-${idx}`

    return div(
      { className: 'mt4', id },
      textarea({
        attrs: { readonly: true, rows: 4 },
        className: 'w-100 pa2 code lh-copy',
        value: `${formatKey(key)}`,
        disabled: true
      }),
      styledButton(onclick, 'Delete')
    )

    function onclick (ev) {
      self.emit(self.state.events.SSH_KEYS_DEL, idx)
    }
  }
}

module.exports = SSHKeys

function formatKey (key) {
  var [head, middle, tail] = key.split(' ')
  var pre = middle.slice(0, 12)
  var end = middle.slice(-12)
  return [head, pre + '...' + end, tail].join(' ')
}

var buttonClass =
    'b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6'

function styledButton (onclick, value, id) {
  return div(
    { onclick, className: 'mv2 mr4 dib' },
    input({
      id,
      className: buttonClass,
      type: 'button',
      value
    })
  )
}

function Error (message) {
  var className = 'w-100 bg-yellow pa3 mv2'
  return message ? div({className}, message) : null
}
