var { section, div } = require('hyperaxe')

var Layout = require('../components/layout')
var Physical = require('./physical')
var Login = require('../session/view')

var TITLE = 'ZeroTier - Edge - Settings'

var physical = new Physical()

module.exports = function (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)
  var layout = Layout(state, emit)

  return state.sessions.cata({
    NotLoaded: login,
    Loading: login,
    Error: login,
    Failure: login,
    Success: () => layout(view)
  })

  function view () {
    return section(
      { },
      div(
        { id: 'settings', className: '' },
        div(
          { className: '' },
          physical.render(state, emit)
        )
      )
    )
  }

  function login () {
    return Login(state, emit)
  }
}
