
module.exports = function (api) {
  return function store (state, emitter) {
    state.sshKeys = []
    state.sshKeysVector = 0
    state.sshKeysLoading = false
    state.sshKeysError = null

    state.events.SSH_KEYS_PUT = 'sshkey:put'
    state.events.SSH_KEYS_DEL = 'sshkey:del'
    state.events.SSH_KEYS_LOAD = 'sshkey:load'

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.SSH_KEYS_PUT, put)
      emitter.on(state.events.SSH_KEYS_DEL, del)
      emitter.on(state.events.SSH_KEYS_LOAD, load)

      emitter.on(state.events.NAVIGATE, navigate)

      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()
      })
    })

    function load () {
      state.sshKeysLoading = true
      render()
      api.get('ssh-keys', function (err, res) {
        state.sshKeysLoading = false
        if (err) {
          state.sshKeysError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.sshKeys = res.body
            state.sshKeysError = null
          } else {
            state.sshKeysError = res.body.message || 'Something went wrong'
          }
        }
        render()
      })
    }

    function put (d) {
      state.sshKeysLoading = true
      render()
      api.put('ssh-keys', { sshKey: d }, function (err, res) {
        state.sshKeysLoading = false
        if (err) {
          state.sshKeysError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.sshKeys = res.body
            state.sshKeysError = null
          } else {
            state.sshKeysError = res.body.message || 'Something went wrong'
          }
        }
        render()
      })
    }

    function del (idx) {
      state.sshKeysLoading = true
      render()
      api.delete('ssh-keys', idx, function (err, res) {
        state.sshKeysLoading = false
        if (err) {
          state.sshKeysError = 'Network error'
        } else {
          if (res.statusCode === 200) {
            state.sshKeysError = null
            load()
          } else {
            state.sshKeysError = res.body.message || 'Something went wrong'
          }
        }
        render()
      })
    }

    function navigate () {
      if (state.route === 'user') {
        load()
      }
    }

    function render () {
      state.sshKeysVector++
      emitter.emit('render')
    }
  }
}
