/* global EventSource */

var Remote = require('../remote-data')

module.exports = function store (state, emitter) {
  state.sessions = Remote('sessions', state, update)
  state.passwords = Remote('passwords', state, update)
  state.logoutEventSource = null

  state.events.SESSIONS_LOGIN = 'sessions:login'
  state.events.SESSIONS_LOGOUT = 'sessions:logout'
  state.events.SESSIONS_CREATE = 'sessions:create'
  state.events.SESSIONS_DELETE = 'sessions:delete'
  state.events.PASSWORD_UPDATE = 'password:update'
  state.events.INIT_CREATE = 'init:create'

  emitter.on('DOMContentLoaded', function () {
    state.sessions.get(function (sessions) {
      sessions.cata({
        NotLoaded: noOp,
        Loading: noOp,
        Error: noOp,
        Failure: state.init.get,
        Success: () => {
          emitter.emit(state.events.SESSIONS_LOGIN)
        }
      })
    })
  })

  emitter.on(state.events.SESSIONS_LOGIN, function (data) {
    render()

    if (!state.logoutEventSource) {
      state.logoutEventSource = new EventSource('/api/sse')

      state.logoutEventSource.onmessage = function (event) {
        try {
          var data = JSON.parse(event.data)
          if (data.type === 'logout') {
            emitter.emit(state.events.SESSIONS_LOGOUT)
          }
        } catch (e) {
          console.log('something went wrong in server sent event source')
        }
      }
    }
  })

  emitter.on(state.events.SESSIONS_CREATE, function (data) {
    state.sessions.post(data, function (sessions) {
      emitter.emit(state.events.SESSIONS_LOGIN)
      render()
    })
  })

  emitter.on(state.events.SESSIONS_DELETE, function (data) {
    state.sessions.delete(function (sessions) {
      render()
    })
  })

  emitter.on(state.events.SESSIONS_LOGOUT, function (data) {
    state.sessions.get(function (sessions) {
      render()
    })
  })

  emitter.on(state.events.INIT_CREATE, function (data) {
    render()
    state.init.post(data, function (init) {
      init.cata({
        NotLoaded: noOp,
        Loading: noOp,
        Error: noOp,
        Failure: noOp,
        Success: gotoWelcome
      })
    })
  })

  emitter.on(state.events.PASSWORD_UPDATE, function (data) {
    data.username = state.sessions.value.username
    state.passwords.put(data, function (res) {})
  })

  function gotoWelcome () {
    state.sessions.get()
    emitter.emit(state.events.PUSHSTATE, '/welcome')
  }

  function update () {
    emitter.emit('update')
  }

  function render () {
    emitter.emit('render')
  }
}

function noOp () {}
