var Nanocomponent = require('nanocomponent')
var h = require('hyperaxe')
var { hr, div, h3, h4, input, label, section, h2, form, header, select, option } = h
var nanologger = require('nanologger')
var log = nanologger('phy')
var merge = require('deepmerge')
var Spinner = require('../components/spinner')

var realInterfaceNames = /^en|^eth|^phy|^wlan/

function regexMatch (re) {
  return function (str) {
    return str.match(re)
  }
}

var labelClass = 'dib w5 f4 b '

class Physical extends Nanocomponent {
  constructor (opts) {
    if (!opts) opts = {}
    super(opts)

    this.loading = true
    this.saving = false
    this.api = opts.api
    this.emit = null
    this.clock = null
    this.listPeersClock = null

    this.interfaces = []
    this.networkIds = []
  }

  createElement (state, emit) {
    log.debug('createElement()')

    this.emit = emit
    this.state = state

    return state.osInterfacesMachine.cata({
      NotAsked: () => section(''),
      Loading: () => InterfacesList(state, emit),
      Success: () => InterfacesList(state, emit),
      Error: () => InterfacesList(state, emit),
      Saving: () => InterfacesList(state, emit)
    })
  }

  update (state, emit) {
    log.debug('update()')

    this.emit = emit
    this.state = state

    var render = (this.clock !== state.interfacesClock) ||
        (this.listPeersClock !== state.listPeersClock)

    this.clock = state.interfacesClock
    this.listPeersClock = state.listPeersClock

    return render
  }

  load () {
    log.debug('load()')

    this.interfacesEdits = merge({}, this.interfaces)
    this.rerender()
  }
}

function InterfacesList (state, emit) {
  var { osInterfaces } = state
  return section(
    form(
      { id: 'physical-interfaces', onsubmit },
      input({ type: 'submit', style: 'display: none' }),
      div(
        header(
          { className: 'h3' },
          h2({ className: 'dib mv0 f2 tracked mr2' }, 'Interfaces'),
          Spinner(isLoading(state)),
          section({ className: 'dib fr' }, Submit(state, emit))
        ),
        Invalid(state, emit),
        osInterfaces
          .filter(regexMatch(realInterfaceNames))
          .map(InterfaceItem(state, emit))
      )
    )
  )

  function onsubmit (ev) {
    ev.preventDefault()
    emit(state.events.PHYSICAL_SUBMIT)
  }
}

function Invalid (state, emit) {
  return state.interfacesMachine.cata({
    NotAsked: naught,
    Loading: naught,
    Success: naught,
    Error: message,
    Saving: naught
  })

  function naught () {
    return null
  }

  function message (err) {
    return div({ className: 'w-100 bg-yellow black pa3 mv2' }, err.message)
  }
}

function InterfaceItem (state, emit) {
  return function interfaceItem_ (iface, interfaceIndex) {
    return [
      Interface(iface, interfaceIndex, state, emit),
      hr({ className: 'mv3 gold b--dashed o-20' })
    ]
  }
}

var wifiRe = /^wlan/
function isWifiInterface (ifaceName) {
  return !!ifaceName.match(wifiRe)
}

function Interface (osIfaceName, idx, state, emit) {
  var config = state.interfaces[osIfaceName]
  var id = `interface-${osIfaceName}-${idx}`

  return section(
    { id, className: 'interface-section pv4' },
    header(
      { className: 'interface-header pv3' },
      h2({ className: 'dib w5 mv0 ttu tracked' }, osIfaceName),
      section(
        { id: id + '-del', className: 'dib v-top' },
        config ? DeleteInterface(osIfaceName) : InterfaceBlank(osIfaceName)
      )
    ),
    config
      ? div(InterfaceConfig(config), WirelessConfig(osIfaceName, config))
      : null
  )

  function WirelessConfig (osIfaceName, config) {
    var id = `wifi-${osIfaceName}`

    var classNameI = 'dib w5'

    return isWifiInterface(osIfaceName)
      ? section(
        { className: 'wireless-config' },
        header(h2('Wi-Fi')),
        section(
          { id, className: 'mt3' },
          label({ className: labelClass }, 'SSID'),
          input({
            className: classNameI,
            type: 'text',
            value: config.essid || null,
            placeholder: 'cool_wifi',
            oninput
          })
        ),
        section(
          { id: id + '-input', className: 'mt3' },
          label({ className: labelClass }, 'Password (PSK)'),
          input({
            className: classNameI,
            type: 'text',
            value: config.psk || null,
            placeholder: 'hunter2',
            oninput: oninput2
          })
        ),
        section(
          { id: id + '-modes', className: 'mt3' },
          Modes(osIfaceName),
          config && config.mode === 'ap'
            ? Channels(osIfaceName)
            : null
        )
      )
      : null

    function oninput (ev) {
      emit(state.events.PHYSICAL_UPDATE, osIfaceName, {
        essid: ev.target.value
      })
    }

    function oninput2 (ev) {
      emit(state.events.PHYSICAL_UPDATE, osIfaceName, { psk: ev.target.value })
    }

    function Channels (ifaceName) {
      var id = `channel-${ifaceName}`
      var className = 'mt3'
      return section(
        { id, className },
        header({ className: 'dib' }, h3({ className: 'mv0 w5' }, 'Channel')),
        div(
          { className: 'dib' },
          select({ onchange }, Object.values(state.constants.WIFI_CHANS).map(options))
        )
      )

      function options (entry) {
        var { channel, frequency } = entry

        var selected = state.interfaces[ifaceName].channel === channel
        var label = `${channel} (${frequency})`

        return option({ value: channel, attrs: selected ? { selected } : {} }, label)
      }

      function onchange (ev) {
        emit(state.events.PHYSICAL_UPDATE, osIfaceName, { channel: Number(ev.target.value) })
      }
    }

    function Modes (ifaceName) {
      var id = `mode-${ifaceName}`
      return section(
        { id },
        header({ className: 'dib' }, h3({ className: 'mv0 w5' }, 'Mode')),
        div(
          { className: 'dib' },
          state.constants.WIFI_MODES.map(Mode)
        )
      )
    }

    function Mode (mode) {
      var checked = mode === config.mode
      var id = `ip-autoconf-${config.name}-${mode}`

      return section(
        { className: 'm2 dib w4' },
        input({
          className: checkboxClass,
          onchange,
          name: `ip-mode-${config.name}`,
          type: 'radio',
          id,
          checked
        }),
        label(
          {
            className: checkboxLabelClass,
            attrs: { for: id }
          },
          mode
        )
      )

      function onchange (ev) {
        emit(state.events.PHYSICAL_UPDATE, osIfaceName, { mode })
      }
    }
  }

  function InterfaceConfig (config) {
    var addressList = addressesFor(osIfaceName, state.addresses)
    var isWifi = isWifiInterface(osIfaceName)

    return section(
      { className: 'interface-config' },
      isWifi
        ? config.mode === 'ap'
          ? Virtual(osIfaceName)
          : null
        : Virtual(osIfaceName),

      section(
        { className: 'interface-addresses' },
        isWifi
          ? config.mode === 'client'
            ? Autoconfs(osIfaceName)
            : null
          : Autoconfs(osIfaceName),
        AddressList(addressList)
      )
    )
  }

  function Virtual (osIfaceName) {
    var id = `physical-${osIfaceName}-virtual`

    return header(
      { className: 'network-id-header mv4' },
      label({ className: 'mv0 w5 dib f4 fw5', attrs: { for: id } }, 'ZeroTier Network'),
      input({
        id,
        className: 'dib w5',
        type: 'text',
        oninput,
        value: config.virtual || null,
        placeholder: '8056c2e21c000001'
      }),
      div(
        { className: 'absolute f6 fw1 ' },
        div({ className: 'dib w5-l' }),
        div({ className: 'dib w5 tr-l' }, isJoined())
      ),
      config.virtual
        ? Blocks(osIfaceName)
        : null
    )

    function oninput (ev) {
      emit(state.events.PHYSICAL_UPDATE, osIfaceName, {
        virtual: ev.target.value
      })
    }

    function isJoined () {
      if (!config.virtual) return null

      var v = state.listNetworks.filter(function (network) {
        return config.virtual === network.id
      })
        .map(function (network) {
          return network.status
        })
      return v[0]
    }

    function Blocks (ifaceName) {
      var id = `autoconf-${ifaceName}`
      return section(
        { id, className: 'interface-autoconf pv2' },
        header(
          { className: 'w5 dib' },
          h3({ className: 'mv0 fw5' }, 'Do Not Bridge')),
        div(
          { className: 'dib' },
          state.constants.BRIDGE_PROHIBIT.map(Block)
        )
      )
    }

    function Block (name) {
      var id = `bridge-block-${config.name}-${name}`
      return div(
        { className: 'dib w4' },
        input({
          className: checkboxClass,
          onchange,
          id,
          type: 'checkbox',
          checked
        }),
        label(
          {
            className: checkboxLabelClass,
            attrs: { for: id }
          },
          name
        )
      )

      function onchange (ev) {
        emit(state.events.BRIDGE_PROHIBIT, osIfaceName, name, ev.target.checked)
      }

      function checked () {
        return config.prohibit && config.prohibit.includes(name)
      }
    }
  }

  function Autoconfs (ifaceName) {
    var id = `autoconf-${ifaceName}`
    return section(
      { id, className: 'interface-autoconf pv2' },
      header(
        { className: 'w5 dib' },
        h3({ className: 'mv0 fw5' }, 'Physical Addresses')),
      div(
        { className: 'dib' },
        state.constants.AUTO_CONFS.map(Autoconf)
      ),
      input({
        className: 'db ml7 mt3 w4',
        type: 'button',
        value: '+ Static',
        onclick
      })
    )

    function onclick () {
      emit(state.events.ADDRESS_CREATE, ifaceName)
    }
  }

  function Autoconf (name) {
    var id = `ip-autoconf-${config.name}-${name}`
    return div(
      { className: 'dib w4' },
      input({
        className: checkboxClass,
        onchange,
        id,
        type: 'checkbox',
        checked
      }),
      label(
        {
          className: checkboxLabelClass,
          attrs: { for: id }
        },
        name
      )
    )

    function onchange (ev) {
      emit(state.events.PHYSICAL_AUTO, osIfaceName, name, ev.target.checked)
    }

    function checked () {
      return config.autoconf && config.autoconf.includes(name)
    }
  }

  function InterfaceBlank (iface) {
    var id = `iface-config-${iface}`

    return input({
      id,
      type: 'button',
      value: 'Configure',
      className: 'w4',
      onclick
    })

    function onclick () {
      emit(state.events.PHYSICAL_CREATE, osIfaceName)
    }
  }

  function AddressList (addresses) {
    var id = `iface-addresses-${osIfaceName}`
    return section({ id }, addresses.map(AddressItem))
  }

  function AddressItem (address, i) {
    var id = `iface-address-${address.tempId}`
    return section(
      { id, className: 'static-address pv3' },
      header(
        { className: 'static-address-header pv1' },
        h3({ className: 'mv0 dib w5' }, `Static Address ${i + 1}: `),
        div(
          { className: 'dib v-top' },
          DeleteAddress(address)
        )
      ),
      Familys(address),
      StaticConfig(address)
    )
  }

  function DeleteInterface (name) {
    var id = `iface-delete-${name}`
    return input({
      id,
      type: 'button',
      value: 'Delete',
      className: 'w4',
      onclick
    })

    function onclick () {
      emit(state.events.PHYSICAL_DELETE, name)
    }
  }

  function StaticConfig (address) {
    var { proto, family } = address
    var static_ = proto === 'static'
    var ipv4 = family === 'ipv4'

    return !static_ ? null : ipv4 ? V4Static(address) : V6Static(address)

    function V4Static (address) {
      var id = `static-address-${address.tempId}`
      return section(
        header(
          { id, className: 'static-addr-addr-header pv1' },
          label({ className: 'w5 dib f5' }, 'Address'),
          input({
            className: 'dib w5',
            type: 'text',
            value: address.address || null,
            placeholder: '10.11.12.13',
            oninput: oninput('address')
          })
        ),
        header(
          { id, className: 'static-addr-mask-header pv1' },
          label({ className: 'w5 dib f5' }, 'Mask'),
          input({
            className: 'dib w5',
            type: 'number',
            value: address.mask || null,
            placeholder: '24',
            oninput: oninputNumber('mask')
          })
        ),
        header(
          { id, className: 'static-addr-gw-header pv1' },
          label({ className: 'w5 dib f5' }, 'Gateway'),
          input({
            className: 'dib w5',
            type: 'text',
            value: address.gateway || null,
            placeholder: '10.11.12.1',
            oninput: oninput('gateway')
          })
        )
      )
    }

    function V6Static (address) {
      var id = `static-address-${address.tempId}`
      return section(
        section(
          { id, className: 'static-addr6-addr pv1' },
          label({ className: 'w5 dib f5' }, 'Address'),
          input({
            style: 'width: 24rem',
            className: 'dib',
            type: 'text',
            value: address.address || null,
            placeholder: '2001:0db8:85a3:0000:0000:8a2e:0370:7334',
            oninput: oninput('address')
          })
        ),
        section(
          { id, className: 'static-addr6-mask pv1' },
          label({ className: 'w5 dib f5' }, 'Mask'),
          input({
            className: 'dib w5',
            type: 'number',
            value: address.mask || null,
            placeholder: '64',
            oninput: oninputNumber('mask')
          })
        )
      )
    }

    function oninput (prop) {
      return function (ev) {
        emit(state.events.ADDRESS_UPDATE, address.tempId, prop, ev.target.value)
      }
    }

    function oninputNumber (prop) {
      return function (ev) {
        emit(
          state.events.ADDRESS_UPDATE,
          address.tempId,
          prop,
          Number(ev.target.value)
        )
      }
    }
  }

  function DeleteAddress (address) {
    var id = `address-delete-${address.tempId}`

    return input({
      onclick,
      value: 'Delete',
      type: 'button',
      className: 'w4',
      id
    })

    function onclick () {
      emit(state.events.ADDRESS_DELETE, address.tempId)
    }
  }

  function Familys (address) {
    return section(
      { className: 'static-addr-family pv1' },
      header(
        { className: 'w5 dib' },
        h4({ className: 'mv0 f5 normal' }, 'Family')
      ),
      state.constants.IP_FAMILIES.map(function (family) {
        var checked = family === address.family
        return section(
          { className: 'dib w4' },
          input({
            className: checkboxClass,
            onchange: onchange(address.tempId, 'family', family),
            name: `ip-family-${address.tempId}`,
            type: 'radio',
            checked
          }),
          label({ className: checkboxLabelClass }, family)
        )
      })
    )

    function onchange (addressKey, prop, val) {
      return function () {
        emit(state.events.ADDRESS_UPDATE, addressKey, prop, val)
      }
    }
  }
}

var checkboxClass = 'mr2'
var checkboxLabelClass = 'lh-copy mr2'

function addressesFor (osIfaceName, addresses) {
  return Object.values(addresses).filter(function (address) {
    return address.iface === osIfaceName
  })
}

function Submit (state, emit) {
  var id = 'edge-physical-submit'
  var edited = state.interfacesEdited ? 'bg-light-red' : null

  return div(
    { id },
    input({
      className: 'v-top ' + edited,
      type: 'button',
      value: 'Save Changes',
      onclick
    }),
    div(
      { className: 'di absolute' },
      Spinner(isSaving(state))
    )

  )

  function onclick () {
    emit(state.events.PHYSICAL_SUBMIT)
  }
}

function isSaving (state) {
  var machines = [state.interfacesMachine]

  return machines.some(function (m) {
    return m.isSaving()
  })
}

function isLoading (state) {
  var machines = [state.interfacesMachine]

  return machines.some(function (m) {
    return m.isLoading()
  })
}

module.exports = Physical
