var choo = require('choo')
var css = require('sheetify')

css('css-spinner')
css('tachyons')

var api = require('./api')()

// var Device = require('./device/view')
var Login = require('./session/view')
var Settings = require('./settings/view')
var Status = require('./status/view')
var Update = require('./software-update/view')
var User = require('./user/view')
var Welcome = require('./welcome')

var edgeStore = require('./device/store')
var httpAdminStore = require('./http-admin/store')
var interfaceStore = require('./settings/store-interfaces')
var sessionStore = require('./session/store')
var sshKeyStore = require('./user/ssh-key-store')
var statusStore = require('./status/store')
var store = require('./store')
var tomlStore = require('./settings/store-toml')
var updateStore = require('./software-update/store')

var app = choo()
app.use(store)
app.use(edgeStore(api))
app.use(httpAdminStore(api))
app.use(interfaceStore(api))
app.use(sessionStore)
app.use(sshKeyStore(api))
app.use(statusStore(api))
app.use(tomlStore(api))
app.use(updateStore(api))
app.route('/', Settings)
// app.route('/device', Device)
app.route('/login', Login)
app.route('/settings', Settings)
app.route('/status', Status)
app.route('/update', Update)
app.route('/user', User)
app.route('/welcome', Welcome)

if (process.env.NODE_ENV !== 'production' && process.env.NODE_ENV !== 'test') {
  app.use(require('choo-devtools')())
  app.debug = true
} else { app.debug = false }

if (!module.parent) app.mount('body')
else module.exports = app
