/* global EventSource */
var Update = require('./remote')('Update')

module.exports = function (api) {
  return function store (state, emitter) {
    state.updateMachine = Update.NotAsked
    state.updateMachineVector = 0
    state.updateLog = []

    state.events.UPDATE_GET = 'software:update'
    state.events.UPGRADE_GET = 'software:upgrade'

    var eventSourceUpdates = null

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.UPDATE_GET, update)
      emitter.on(state.events.UPGRADE_GET, upgrade)
    })

    function update () {
      if (!eventSourceUpdates) {
        state.updateLog = []
        eventSourceUpdates = new EventSource('/api/software/update')

        eventSourceUpdates.onmessage = function (event) {
          try {
            var data = JSON.parse(event.data)

            if (data.type === 'exit') {
              state.updateLog.push(
                data.value === 0
                  ? String.fromCodePoint(0x2728) // :sparkles:
                  : String.fromCodePoint(0x1F6A8) // :rotating_light: 🚨
              )
              eventSourceUpdates.close()
              eventSourceUpdates = null
              render()
            } else if (data.value) {
              state.updateLog.push(data.value)
              render()
            }
          } catch (e) {
            console.log('something went wrong in server sent event source')
          }
        }
      }
    }

    function upgrade () {
      if (!eventSourceUpdates) {
        state.updateLog = []
        eventSourceUpdates = new EventSource('/api/software/upgrade')

        eventSourceUpdates.onmessage = function (event) {
          try {
            var data = JSON.parse(event.data)

            if (data.type === 'exit') {
              state.updateLog.push(
                data.value === 0
                  ? String.fromCodePoint(0x2728) // :sparkles:
                  : String.fromCodePoint(0x1F6A8) // :rotating_light: 🚨
              )
              eventSourceUpdates.close()
              eventSourceUpdates = null
              render()
            } else if (data.value) {
              state.updateLog.push(data.value)
              render()
            }
          } catch (e) {
            console.log('something went wrong in server sent event source')
          }
        }
      }
    }

    function render () {
      state.updateMachineVector++
      emitter.emit('render')
    }
  }
}
