var Toml = require('./remote')('Edge')

module.exports = function (api) {
  return function store (state, emitter) {
    state.toml = ''
    state.tomlMachine = Toml.NotAsked
    state.tomlMachineVector = 0

    state.events.TOML_GET = 'toml:get'

    emitter.on(state.events.DOMCONTENTLOADED, function () {
      emitter.on(state.events.SESSIONS_LOGIN, function (data) {
        navigate()
      })

      function get () {
        state.tomlMachine = Toml.Loading
        render()

        api.get('toml', function (err, res) {
          if (err) {
            state.tomlMachine = Toml.Error(err)
            state.toml = {}
          } else {
            if (res.statusCode === 200) {
              state.tomlMachine = Toml.Success(res.body || {})
              state.toml = res.body || ''
            } else {
              state.tomlMachine = Toml.Error(res.body)
              state.toml = ''
            }
          }

          render()
        })
      }

      function navigate () {
        if (state.route === 'settings') {
          state.tomlMachine.cata({
            NotAsked: get,
            Loading: noop,
            Success: noop,
            Error: get,
            Saving: noop
          })
        }
      }

      function render () {
        state.tomlMachineVector++
        emitter.emit('render')
      }
    })
  }
}

function noop () {}
