var { h2, section, div, p, form, fieldset, label, input } = require('hyperaxe')

module.exports = function Setup (state, emit) {
  var password1 = 'setup-password1'
  var password2 = 'setup-password2'
  var usernameId = 'setup-username'

  return section(
    { id: 'edge-setup', className: 'flex justify-center' },
    div(
      { className: 'w-100' },
      h2({ className: 'db mv0 f2 tracked pb2' }, 'Initial Setup'),
      p(
        { className: 'mt4' },
        `Let's create the first account on this ZeroTier Edge`
      ),
      form(
        { onsubmit: submit, id: 'setup-form', className: 'bn ma0 pa0' },
        fieldset(
          { className: 'bn mh0 ph0' },

          label(
            { className: 'f7 fw1', attrs: { for: usernameId } },
            'Username'
          ),
          input({
            className: 'db pa1 w5',
            type: 'text',
            id: usernameId,
            required: true,
            minlength: 4,
            maxlength: 32,
            oninput: onPwChange
          }),
          div({
            className: 'h2 f7 light-red',
            id: 'setup-username-message'
          })
        ),

        fieldset(
          { className: 'bn mh0 ph0' },
          label({ className: 'f7 fw1', attrs: { for: password1 } }, 'Password'),
          password(password1, onPwChange),
          div({ className: 'h2 f7 light-red', id: 'setup-password-message' })
        ),

        fieldset(
          { className: 'bn mh0 ph0' },
          label(
            { className: 'f7 fw1', attrs: { for: password2 } },
            'Repeat Password'
          ),
          password(password2, onPwChange),
          div({ className: 'h2 f7 light-red', id: 'setup-password2-message' })
        ),

        input({
          className: 'w4',
          type: 'submit',
          id: 'setup-submit-user',
          value: 'Create'
        })
      )
    )
  )

  function onPwChange (ev) {
    var pw1 = document.querySelector('#' + password1)
    var pw2 = document.querySelector('#' + password2)
    var username = document.querySelector('#' + usernameId)

    if (pw1.value !== pw2.value) {
      pw2.setCustomValidity('Password fields must match')
    } else if (username.value.toLowerCase() === 'root') {
      username.setCustomValidity('root is not an allowed username')
    } else {
      pw2.setCustomValidity('')
      username.setCustomValidity('')
    }
  }

  function submit (ev) {
    ev.preventDefault()

    if (ev.target.checkValidity()) {
      var password = document.querySelector('#' + password1).value
      var username = document.querySelector('#' + usernameId).value

      var data = { password: password, username: username }

      emit(state.events.INIT_CREATE, data)
    }
  }
}

function password (id, oninput) {
  return input({
    id: id,
    oninput: oninput,
    minlength: 4,
    autocomplete: 'new-password',
    required: true,
    className: 'db pa1 w5',
    type: 'password'
  })
}
