var { section, div, h1, p, a } = require('hyperaxe')

var TITLE = 'ZeroTier - Edge - Welcome'

module.exports = function InitView (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)
  var Layout = require('./components/layout')(state, emit)

  return Layout(view)
}

function view (state, emit) {
  return section(
    { className: 'mt0' },
    div(
      { id: 'welcome' },
      h1({ className: 'db mv0 f2 tracked pb2' }, 'Welcome'),
      p(`Now you can configure this ZeroTier Edge`),
      p(a({ href: '/settings' }, 'Settings'))
    )
  )
}
