var request = require('request')

module.exports = function () {
  return {
    get,
    put,
    post,
    delete: del
  }

  function get (path, callback) {
    request.get(`/api/${path}`, { json: true }, function (err, res, body) {
      if (err) return callback(err)

      callback(null, res)
    })
  }

  function put (path, data, callback) {
    request.put(`/api/${path}`, { json: true, data }, function (err, res, body) {
      if (err) return callback(err)

      callback(null, res)
    })
  }

  function post (path, data, callback) {
    request.post(`/api/${path}`, { json: true, data }, function (err, res, body) {
      if (err) return callback(err)

      callback(null, res)
    })
  }

  function del (path, id, callback) {
    request.del(`/api/${path}/${id}`, { json: true }, function (err, res, body) {
      if (err) return callback(err)

      callback(null, res)
    })
  }
}
