var { section, div } = require('hyperaxe')

var _Setup = require('./setup')
var _Login = require('./login')

var TITLE = 'ZeroTier - Edge - Login'
var Layout = require('../components/layout')

module.exports = function LoginForm (state, emit) {
  if (state.title !== TITLE) emit(state.events.DOMTITLECHANGE, TITLE)

  return Layout(state, emit)(view)

  function view (state, emit) {
    return section(
      { id: 'login-or-setup' },
      div(
        { className: '' },
        div(
          state.init.cata({
            NotLoaded: () => null,
            Loading: () => null,
            Error: b => b.init ? Login : Setup,
            Failure: b => b.init ? Login : Setup,
            Success: b => b.init ? Login : Setup
          })
        )
      )
    )
  }

  function Setup () {
    return _Setup(state, emit)
  }

  function Login () {
    return _Login(state, emit)
  }
}
